:root {
  --main-bg-color: #007585;
  --header-text-color: #fff;
  --navitem-border-color: #000000;
  --navitem-border-active-color: #fff;
  --navitem-active-color: #038c9e;
  --footer-icon-backlit-color: #fff;
}

.tinyLogo {
  width: 50;
  height: 50;
}
.logo {
  width: 66;
  height: 58;
}
.headerGroup {
  background: var(--main-bg-color);
  /* min-height: 200px; */
  display: flex;
  flex-direction: column-reverse;
  justify-content: center;
  align-items: center;
 
}
.headerGroup h1 {
  margin: 0;
  padding-top: 5px;
  padding-bottom: 5px;
  text-align: center;
  color: var(--header-text-color);
  font-size: 3rem;
}

.nav.nav-tabs {
  list-style-type: none;
  display: flex;
  flex-direction: column;
  padding-inline-start: 0rem; /*override*/
  width: 100%;  
}
.nav.nav-tabs li {
  font-size: 1.5rem;
  
}

.nav-item {
  margin: 0.05rem 0.5rem;
  /* background-color: #f9b713;  */
  cursor: pointer;
  text-align: center;
  border: var(--navitem-border-color) solid 0.25rem;
  border-radius: 0.5rem;
  padding: 0rem 1rem;
}
.nav-item.active {
  background-color: var(--navitem-active-color);
  border: var(--navitem-border-active-color) solid 0.25rem;
  border-radius: 0.5rem;
  padding: 0rem 1rem;
}
.footerGroup {
  text-align: center;
  background-color: var(--main-bg-color);
}
.footerGroup img:hover {
  opacity: 1;
  filter: drop-shadow(0px 0px 3px var(--footer-icon-backlit-color));
}
.footerGroup img {
  padding: 0rem 0.25rem;
  padding-top: 0.3rem;
  opacity: 0.6;
}
.sm {
  font-size: 0.9rem;
  margin-block-start: 0.05rem;
  margin-block-end: 0.05rem;
}

.edu-exp {
  padding: 0.15rem;
  /* display: flex; */
}
.container {
  margin: 0.25rem;
  width: 99%;
  display: flex;
  flex-direction: column;
  /* flex-wrap: wrap; */
  flex-grow: 1;
}
.card {
  margin: 0.25rem 0.25rem;
  background-color: var(--main-bg-color);
  border-radius: 0.25rem;
  padding: 0.5rem;
  flex: 1;
  flex-basis: 33%;
  position: relative;
  min-height: 12rem;
}
.card-header {
  display: flex;
  opacity: 0;
  padding: 0.25rem;
  flex-direction: column;
  justify-content: space-evenly;
 
}
.card-header a {
  padding: 0rem 0.5rem;
 
}
.card .card-header{
  background-color: hsla(0,0%,100%,.5);
  height: 100%;
  left: 0;
  opacity: 0;
  position: absolute;
  top: 0;
  transition: opacity .3s ease;
  width: 100%;
}
.card-body {
  padding: 0.25rem;
}
.card-text {
  opacity: 0;
}
.card:hover .card-header {
  opacity: 1;
}
.card:hover .card-text {
  opacity: 1;
}

.form * {
  padding: 0.25rem;
  margin: 0.25rem 0rem;
}

.downloadres {
  display: flex;
}

.downloadbtn {
  flex-grow: 1;
  margin: .05rem .5rem;
  min-height: 35px;
}

@media screen and (min-width: 422px) {
  .container {
    flex-direction: row;
    flex-wrap: wrap;
  }
  .card {
    min-width: 180px;
  }
}

@media screen and (min-width: 658px) {
  .card {
    min-height: 400px;
  }
  .card .card-header a {
    font-size: 2rem;
  }
}
@media screen and (min-width: 900px) {
  .headerGroup {
    flex-direction: row;
  }
  .nav.nav-tabs {
    flex-direction: row;
  }
 
}
@media screen and (min-width: 1000px) {

  .card{
    flex-basis: 28%;
  }

  .edu-exp {
    display: flex;
    padding: 0.5rem;
  }

  .container {
    margin: auto;
    max-width: 90%;
  }

}
